






















// @ is an alias to /src
import { Component, Vue } from 'vue-property-decorator';

@Component({
  name: 'home',
})
export default class Home extends Vue {
  BUNGIE_NET_URL = 'https://www.bungie.net';
  API_ENDPOINT = this.BUNGIE_NET_URL + '/Platform';
  clientId = '31285';
  apiKey = 'bb4feabcb1894b4e91eb270c70aebe7f';
  state = '6e898a4e75f2';
  accessToken?: string;
  membership?: {id: string; type: string};
  manifest: any = {};
  manifestVersion = '';

  get loginUrl() {
    return `${this.BUNGIE_NET_URL}/en/oauth/authorize?client_id=${this.clientId}&response_type=code&state=${this.state}`;
  }

  async created() {
    const query = this.$route.query;
    
    if (query.state && query.state !== this.state) {
      return;
    }

    this.accessToken = localStorage.getItem('accessToken') || '';
    this.membership = JSON.parse(localStorage.getItem('membership') || 'null');

    // if (!this.accessToken && typeof query.code === 'string') {
    //   await this.getAccessToken(query.code);
    // }

    // if (!this.accessToken) {
    //   return;
    // }

    // if (!this.membership) {
    //   await this.getDestinyMembership();
    // }

    // if (!this.membership) {
    //   return;
    // }

    fetch(`${this.API_ENDPOINT}/Destiny2/Manifest/`, {
      method: 'GET',
      headers: {
        'X-API-Key': this.apiKey,
        // 'Authorization': `Bearer ${this.accessToken}`,
      },
    })
    .then(res => res.json())
    .then(json => {
      this.manifest = json.Response;
      this.manifestVersion = this.manifest.version;
      delete this.manifest.version;

      Object.keys(this.manifest).forEach(key => {
        if (this.manifest[key]['ja']) {
          this.manifest[key] = this.manifest[key]['ja'];
        }
        if (Array.isArray(this.manifest[key])) {
          this.manifest[key].forEach((item: any, index: number) => {
            if (item.path) {
              this.manifest[key][index] = item.path;
            }
          });
        }
      });
    })
    .catch(err => {
      this.accessToken = '';
      localStorage.removeItem('accessToken');
      localStorage.removeItem('membership');
    });
  }

  getAccessToken(code: string) {
    return fetch(`${this.API_ENDPOINT}/app/oauth/token/`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
      body: `grant_type=authorization_code&code=${code}&client_id=${this.clientId}`,
    })
    .then(res => res.json())
    .then(json => {
      this.accessToken = json.access_token;
      localStorage.setItem('accessToken', this.accessToken || '');
    });
  }

  getDestinyMembership() {
    return fetch(`${this.API_ENDPOINT}/User/GetMembershipsForCurrentUser/`, {
      method: 'GET',
      headers: {
        'X-API-Key': this.apiKey,
        'Authorization': `Bearer ${this.accessToken}`,
      },
    })
    .then(res => res.json())
    .then(json => {
      const membership = json.Response.destinyMemberships[0];
      this.membership = {
        type: membership.membershipType,
        id: membership.membershipId,
      };
      localStorage.setItem('membership', JSON.stringify(this.membership));
    })
  }

  get() {
    if (!this.membership) return;
    return fetch(`${this.API_ENDPOINT}/Destiny2/${this.membership.type}/Profile/${this.membership.id}/Item/6917529134990411491/?components=300,301,302`, {
      method: 'GET',
      headers: {
        'X-API-Key': this.apiKey,
        'Authorization': `Bearer ${this.accessToken}`,
      },
    })
    .then(res => res.json())
  }
}
